@import url("https://use.typekit.net/phl0vkc.css");
@import url("https://use.typekit.net/phl0vkc.css");
:root {
  --primary-font: "forma-djr-text", sans-serif;
  --secondary-font: "kallisto", sans-serif;
  --text-base-size: 1rem;
  --text-xs: calc(var(--text-base-size) * 0.75);
  --text-sm: calc(var(--text-base-size) * 0.875);
  --text-md: calc(var(--text-base-size));
  --text-lg: calc(var(--text-base-size) * 1.5);
  --text-xl: calc(var(--text-base-size) * 2);
}

h1,
.text-xl {
  font-size: var(--text-xl);
}

h2,
.text-lg {
  font-size: var(--text-lg);
}

h3,
.text-md {
  font-size: var(--text-md);
}

h4,
.text-sm {
  font-size: var(--text-sm);
}

h5,
.text-xs {
  font-size: var(--text-xs);
}

p {
  font-size: var(--text-md);
}

@media (min-width: 425px) {
  :root {
    --text-base-size: 1.5rem;
  }
}